/*
 SCROLL 
 */
/*  width */
::-webkit-scrollbar {
  width: 5px; }

/* background */
::-webkit-scrollbar-track {
  background-color: linear-gradient(to bottom, #a1a1a1, #a1a1a1); }

/* bar color */
::-webkit-scrollbar-thumb {
  background: #f0f0f0; }

/* Disable Button */
::-webkit-scrollbar-button {
  display: none; }

/* loading mark */
.loading {
  position: fixed;
  top: 50%;
  left: 50%;
  border: 8px solid #ff8e8e;
  border-top: 8px solid #3498db;
  border-radius: 50%;
  height: 50px;
  width: 50px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite; }

/* title */
.Title {
  text-align: center;
  margin-left: auto;
  margin-right: auto; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f1f1f1;
  margin-left: 20%;
  margin-right: 20%; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

@-webkit-keyframes slideUp {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 10px, 0);
            transform: translate3d(0, 10px, 0); }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0); } }

@keyframes slideUp {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 10px, 0);
            transform: translate3d(0, 10px, 0); }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0); } }

@-webkit-keyframes sizeUp {
  from {
    -webkit-transform: scale(1);
            transform: scale(1); }
  to {
    -webkit-transform: scale(5);
            transform: scale(5); } }

@keyframes sizeUp {
  from {
    -webkit-transform: scale(1);
            transform: scale(1); }
  to {
    -webkit-transform: scale(5);
            transform: scale(5); } }

@-webkit-keyframes spin {
  from {
    -webkit-transform: scale(1) rotate(0deg);
            transform: scale(1) rotate(0deg); }
  50% {
    -webkit-transform: scale(1.3) rotate(180deg);
            transform: scale(1.3) rotate(180deg); }
  to {
    -webkit-transform: scale(1) rotate(360deg);
            transform: scale(1) rotate(360deg); } }

@keyframes spin {
  from {
    -webkit-transform: scale(1) rotate(0deg);
            transform: scale(1) rotate(0deg); }
  50% {
    -webkit-transform: scale(1.3) rotate(180deg);
            transform: scale(1.3) rotate(180deg); }
  to {
    -webkit-transform: scale(1) rotate(360deg);
            transform: scale(1) rotate(360deg); } }

@-webkit-keyframes leftIn {
  from {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0); }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0); } }

@keyframes leftIn {
  from {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0); }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0); } }

@-webkit-keyframes leftOut {
  from {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0); }
  to {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0); } }

@keyframes leftOut {
  from {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0); }
  to {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0); } }

@-webkit-keyframes rightIn {
  from {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0); }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0); } }

@keyframes rightIn {
  from {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0); }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0); } }

@-webkit-keyframes rightOut {
  from {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0); }
  to {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0); } }

@keyframes rightOut {
  from {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0); }
  to {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0); } }

/* ##Device = Laptops, Desktops
    ##Screen = B/w 1025px to 1280px */
/* ##Device = Tablets, Ipads (portrait)
    ##Screen = B/w 768px to 1024px */
@media (min-width: 768px) and (max-width: 1024px) {
  body {
    margin-left: 10%;
    margin-right: 10%; } }

/* ##Device = Tablets, Ipads (landscape)
    ##Screen = B/w 768px to 1024px */
/* ##Device = Low Resolution Tablets, Mobiles (Landscape)
    ##Screen = B/w 481px to 767px */
@media (min-width: 481px) and (max-width: 767px) {
  body {
    margin-left: 5%;
    margin-right: 5%; } }

/* ##Device = Most of the Smartphones Mobiles (Portrait)
    ##Screen = B/w 320px to 479px */
@media (max-width: 480px) {
  body {
    margin-left: 5%;
    margin-right: 5%; } }

.Search {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12); }

.Search-input {
  width: 100%;
  height: 100%;
  outline: none;
  border: 0;
  padding: 0;
  margin: 0; }

.Map-name {
  margin: 24px 8px 8px 8px;
  white-space: nowrap;
  overflow: hidden;
  font-weight: 700;
  color: rgba(134, 83, 216, 0.952); }

.Map-name::after {
  margin-left: 30px;
  display: inline-block;
  content: "";
  vertical-align: middle;
  height: 1px;
  background: -webkit-gradient(linear, left top, right top, from(rgba(0, 225, 255, 0.35)), to(rgba(255, 0, 225, 0.35)));
  background: -webkit-linear-gradient(left, rgba(0, 225, 255, 0.35), rgba(255, 0, 225, 0.35));
  background: linear-gradient(to right, rgba(0, 225, 255, 0.35), rgba(255, 0, 225, 0.35));
  width: 100%; }

div[class^="Monster-"] {
  vertical-align: middle;
  display: inline-block;
  text-align: center;
  min-width: 10%; }

.Monster {
  margin: 8px;
  padding: 8px;
  border-radius: 2px;
  background: #fff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12); }

.Monster-name {
  width: 100px;
  word-wrap: break-word; }

.Monster-image {
  width: 100px; }

.disable {
  display: none; }

